"use client";
import React, { useState } from "react";
import Image from "next/image";

export default function Home() {
  // Gallery UI with 100 photos, centered, 50% of desktop size, with next/prev controls

  // Generate 100 placeholder image URLs (you can replace these with your own)
  const images = Array.from(
    { length: 100 },
    (_, i) => `https://picsum.photos/seed/${i + 1}/800/600`
  );

  const [current, setCurrent] = useState(0);

  const handlePrev = () => {
    setCurrent((prev) => (prev === 0 ? images.length - 1 : prev - 1));
  };

  const handleNext = () => {
    setCurrent((prev) => (prev === images.length - 1 ? 0 : prev + 1));
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
        minHeight: "80vh",
      }}
    >
      <div
        style={{
          width: "50vw",
          maxWidth: "800px",
          aspectRatio: "4/3",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          position: "relative",
          background: "#f5f5f5",
          borderRadius: "16px",
          boxShadow: "0 2px 16px rgba(0,0,0,0.08)",
          overflow: "hidden",
        }}
      >
        <button
          onClick={handlePrev}
          style={{
            position: "absolute",
            left: 10,
            zIndex: 2,
            background: "rgba(255,255,255,0.7)",
            border: "none",
            borderRadius: "50%",
            width: 40,
            height: 40,
            fontSize: 24,
            cursor: "pointer",
          }}
          aria-label="Previous photo"
        >
          &#8592;
        </button>
        {/* 
          NOTE: To use external images with next/image, you must add the domain
          "picsum.photos" to the images.domains array in your next.config.js:
          module.exports = {
            images: {
              domains: ['picsum.photos'],
            },
          }
        */}
        <Image
          src={images[current]}
          alt={`Photo ${current + 1}`}
          fill
          style={{ objectFit: "contain" }}
          sizes="(max-width: 800px) 100vw, 800px"
          priority
        />
        <button
          onClick={handleNext}
          style={{
            position: "absolute",
            right: 10,
            zIndex: 2,
            background: "rgba(255,255,255,0.7)",
            border: "none",
            borderRadius: "50%",
            width: 40,
            height: 40,
            fontSize: 24,
            cursor: "pointer",
          }}
          aria-label="Next photo"
        >
          &#8594;
        </button>
      </div>
      <div style={{ marginTop: 16, color: "#666" }}>
        {current + 1} / {images.length}
      </div>
    </div>
  );
}
